"use client";

import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabaseClient";
import { DotLottieReact } from "@lottiefiles/dotlottie-react";
import Card from "@/components/Card/Card";
import Header from "@/components/Header/Header";
import Dropdown from "@/components/Dropdown/Dropdown";
import styles from "./page.module.css";

export default function Home() {
  const [websites, setWebsites] = useState([]);
  const [loading, setLoading] = useState(true);

  const [pageTypes, setPageTypes] = useState([]);
  const [industries, setIndustries] = useState([]);
  const [stylesList, setStylesList] = useState([]);

  const [selectedPageType, setSelectedPageType] = useState("all");
  const [selectedIndustry, setSelectedIndustry] = useState("all");
  const [selectedStyle, setSelectedStyle] = useState("all");

  useEffect(() => {
    const fetchFilters = async () => {
      const { data, error } = await supabase.from("sites").select("page_type, industry, style");

      if (error) {
        console.error("Failed to fetch filter data:", error);
        return;
      }

      const pageTypeSet = new Set();
      const industrySet = new Set();
      const styleSet = new Set();

      data.forEach((site) => {
        if (site.page_type) pageTypeSet.add(site.page_type);
        if (site.industry) industrySet.add(site.industry);
        if (site.style) styleSet.add(site.style);
      });

      const formatOptions = (set) => [
        { label: "All", value: "all" },
        ...Array.from(set).map((v) => ({ label: v, value: v })),
      ];

      setPageTypes(formatOptions(pageTypeSet));
      setIndustries(formatOptions(industrySet));
      setStylesList(formatOptions(styleSet));
    };

    fetchFilters();
    fetchSites(); // initial load
  }, []);

  const fetchSites = async (filters = {}) => {
    setLoading(true);

    let query = supabase.from("sites").select("*");

    if (filters.page_type && filters.page_type !== "all") {
      query = query.eq("page_type", filters.page_type);
    }
    if (filters.industry && filters.industry !== "all") {
      query = query.eq("industry", filters.industry);
    }
    if (filters.style && filters.style !== "all") {
      query = query.eq("style", filters.style);
    }

    const { data, error } = await query;

    if (error) {
      console.error("Error fetching sites:", error);
    } else {
      setWebsites(data);
    }

    setLoading(false);
  };

  const handlePageTypeChange = (option) => {
    const value = option.value;
    setSelectedPageType(value);
    fetchSites({ page_type: value, industry: selectedIndustry, style: selectedStyle });
  };

  const handleIndustryChange = (option) => {
    const value = option.value;
    setSelectedIndustry(value);
    fetchSites({ page_type: selectedPageType, industry: value, style: selectedStyle });
  };

  const handleStyleChange = (option) => {
    const value = option.value;
    setSelectedStyle(value);
    fetchSites({ page_type: selectedPageType, industry: selectedIndustry, style: value });
  };

  return (
    <div className={styles.page}>
      <main className={styles.main}>
        <Header />

        <section className={styles.filters}>
          <Dropdown options={pageTypes} onSelect={handlePageTypeChange} />
          <Dropdown options={industries} onSelect={handleIndustryChange} />
          <Dropdown options={stylesList} onSelect={handleStyleChange} />
        </section>

        <section className={styles.website}>
          {loading ? (
            <DotLottieReact
              src="https://lottie.host/85d7c3ff-a5af-4ba0-aced-d3c75753f1c0/siXo0yfAz4.lottie"
              loop
              autoplay
            />
          ) : websites.length === 0 ? (
            <p>No matching websites found.</p>
          ) : (
            websites.map((site) => (
              <Card
                key={site.id}
                title={site.name}
                url={site.base_url}
                src={site.thumbnail}
              />
            ))
          )}
        </section>
      </main>
    </div>
  );
}
